<template>
  <van-cell-group :border="false">
    <template #title>
      <span class="title">{{ title }}</span>
    </template>
    <div class="wrapper">
      <div class="left">
        <van-cell :border="false" class="kv" title="支付宝账号">
          {{ payment.alipayAccount | hide(hide, "支付宝") }}
          <template #right-icon>
            <button
              v-clipboard="hide ? '' : payment.alipayAccount || ''"
              class="copy copyBtn"
              @success="$root.copySuccessful"
            />
          </template>
        </van-cell>
        <van-cell :border="false" class="kv" title="姓名">
          {{ payment.realName | hide(hide, "**") | safeRealName }}
