                 @click="preview(hide, $root.getImageUrl(payment.receiptCode))"/>
    </div>
  </van-cell-group>
</template>

<script>
import {ImagePreview} from 'vant';

export default {
  props: {
    title: String,
    hide: Boolean,
    payment: {
      default: () => ({
        account: "***",
        realName: "**",
        receiptCode: "",
      }),
    },
  },
  methods: {
    preview(hide, url) {
      if (!hide) ImagePreview([url])
    }
  }
};
</script>

