<template>
  <van-cell-group :border="false">
    <template #title>
      <span class="title">{{ title }}</span>
      <slot name="title-right"></slot>
    </template>
    <van-cell :border="false" class="kv" title="银行名称">
      {{ findBank.bankName | hide(hide, "*****") }}
    </van-cell>
    <van-cell :border="false" class="kv" title="开户所在地">
      {{ getAddrFromCode(payment.province, payment.city) | hide(hide, "*** ***") }}
    </van-cell>
    <van-cell :border="false" class="kv" title="开户支行">
      {{ payment.bankBranch | hide(hide, "*******") }}
    </van-cell>
    <van-cell :border="false" class="kv" title="姓名">
      {{ payment.realName | hide(hide, "**") }}
      <template #right-icon>
        <div
          v-clipboard="hide ? '' : payment.realName || ''"
          class="copy copyBtn"
          @success="$root.copySuccessful"
        ></div>
      </template>
    </van-cell>
    <van-cell :border="false" class="kv" title="银行卡号">
      {{ payment.bankNo | hide(hide, "**** **** **** **** ****") }}
      <template #right-icon>
        <div
          v-clipboard="hide ? '' : payment.bankNo || ''"
          class="copy copyBtn"
          @success="$root.copySuccessful"
        ></div>
      </template>
    </van-cell>
  </van-cell-group>
</template>

<script>
import {loadAreaList} from "@/vendor/lazyLoads";

export default {
  props: {
    title: String,
    hide: Boolean,
    payment: {
      default: () => ({
        bankName: "******",
        province: '',
        city: '',
        bankBranch: "*******",
        realName: "**",
        bankNo: "**** **** **** **** ****",
      }),
    },
  },
  data() {
    return {
      areaList: {
        province_list: {},
        city_list: {},
      },
    };
  },
  computed: {
    findBank() {
      return this.$root.banks.find((item) => item.bankId == this.payment.bankId) || {};
    },
  },
  async created() {
    this.areaList = (await loadAreaList()).areaList || {
      province_list: {},
      city_list: {},
    };
  },
  methods: {
    getAddrFromCode(province, city) {
      return this.areaList.province_list[province] + " " + this.areaList.city_list[city];
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-weight: 600;
}
</style>
